.treasury-allocation-router-state-container {

  .parsed-pasted-bid-table-container {
    margin-top: 25px;
    max-width: 100%;

    & > .parsed-pasted-bid-table {
      overflow: auto;
      max-height: 455px;

      .pasted-bid-row-matched {
        background: #cbf4cb;
      }

      tbody > tr > td:nth-last-child(-n+5) { text-align: right; }
    }
  }

  .original-request-row {
    display: block;
    margin-bottom: 5px;

    &.multiple-requests {
      padding: 2px 0;
      font-size: 90%;
      border-radius: 4px;
      color: #c7254e;
      background-color: #f9f2f4;

      & > .glyphicon-remove {
        padding: 0 4px 0 15px;
        font-size: 90%;
        cursor: pointer;
      }
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

}